<template>
  <div class="container">
    <div class="content">
      <h1 class="content-title">Нужно SEO-продвижение для Вашего сайта?
        <span>Мы подберем вариант</span></h1>
      <my-form>
        <p class="form-caption">Оставьте заявку на <span>Бесплатную консультацию</span> специалиста!</p>
        <form class="form">
          <div class="flex">
            <div class="form-row" role="username">
              <input id="form-name" type="text" placeholder="ВАШЕ ИМЯ" required>
            </div>
            <div class="form-row" role="usernumber">
              <input id="form-phone" type="text" placeholder="ВАШ НОМЕР" required>
            </div>
            <button type="submit" @click.prevent class="btn btn-primary">отправить</button>
          </div>
        </form>
      </my-form>
      <div class="seo-rating">
        <h2>TOP 1</h2>
        <span>В РЕЙТИНГЕ ЛУЧШИХ ВЕБ-СТУДИЙ ДАЛЬНЕГО ВОСТОКА</span>
      </div>
    </div>

  </div>
</template>
<script>
  import Form from '../Global/Form'
  export default {
    components: {
      'my-form': Form
    }
  }
</script>
<style lang="scss">
  .content {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 140px;
    padding-bottom: 28px;
    &-title {
      font-family: 'Roboto', sans-serif;
      font-size: 34px;
      text-align: center;
      font-weight: 300;
      color: #fff;
      margin-bottom: 40px;
      span {
        letter-spacing: -.3px;
      }
     }
  }
  .seo-rating {
    position: absolute;
    margin: 0 auto;
    top: 51px;
    left: 50%;
    transform: translateX(-50%);
    text-align: center;
    color: #fff;
    max-width: 410px;
    width: 100%;
    padding: 18px 40px 16px;
    background: #df7f35;
    text-transform: uppercase;
    h2 {
      font-family: 'Roboto';
      font-size: 51px;
      line-height: 1;
      font-weight: 700;
    }
    span {
      font-size: 15px;
      line-height: 1;
    }
  }

  @media only screen and (min-width: 1200px) {
    .content {
      padding-top: 125px;
      padding-bottom: 118px;
      &-title {
        font-family: 'Roboto', sans-serif;
        font-size: 48px;
        font-weight: 300;
        margin-bottom: 49px;
        letter-spacing: .9px;
      }
    }
    .seo-rating {
      top: auto;
      bottom: 0;
      left: 50%;
      transform: translate(-50%,44%);
    }
  }
</style>



