<template>
  <div class="header-nav" ref="navBar">
    <div class="container">
      <div class="flex">
        <a href="#!" class="header-logo">
        </a>
        <a href="tel: (4212)25-30-85" class="header-phone">(4212)25-30-85</a>
        <div ref="burger" :class="[{'is-active': active}, 'burger']"
             @click.prevent="active = !active"
        >
          <div class="burger-box">
            <div class="burger-inner"></div>
          </div>
        </div>
      </div>
      <transition name="view">
        <nav v-show="active" class="nav">
          <ul class="nav-menu">
            <li class="nav-menu-item" v-for="item of navigationMenu">
              <a class="nav-menu-link" href="#!">{{item}}</a>
            </li>
          </ul>
        </nav>
      </transition>
    </div>
  </div>
</template>
<script>
  import {mapState} from 'Vuex'
  export default {
    data() {
      return {
        active: false,
      }
    },
    mounted() {
      window.addEventListener('scroll', this.isScroll);
      document.body.addEventListener('keydown', this.showMenu)
    },
    methods: {
      isScroll() {
        this.$refs['navBar'].style.background = (pageYOffset > 50) ? 'rgb(57, 57, 57)' : ''
      },
      showMenu(e) {
        let screenWidth = document.body.clientWidth;
        console.log(screenWidth)
        if (screenWidth >= 978 && screenWidth < 1200 ) {
          if (e.keyCode === 27) this.active = false;
          if (e.keyCode === 77) this.active = !this.active;
        }
      }
    },
    watch: {
      active() {
        if (this.active) {
          document.body.style.overflow = 'hidden';
        } else {
          document.body.style.overflow = '';
        }
        // document.body.style.overflow = this.active ? 'hidden' : '';
      },
    },
    computed: {
      ...mapState(['navigationMenu']),
    },
    beforeDestroy() {
      window.removeEventListener('scroll', this.isScroll);
    }
  }
</script>
<style lang="scss" scoped>
  // Mobile Styles
  .header {
    &-nav {
      font-family: 'Roboto', sans-serif;
      font-weight: 500;
      position: fixed;
      top: 0;
      left: 0;
      z-index: 1000;
      width: 100%;
      background: rgba(57, 57, 57, .5);
      padding: 5px 0;
      transition: background .3s ease;
    }
    &-logo {
      position: relative;
      z-index: 9999;
      height: 40px;
      width: 62px;
      background: url("/static/img/icons_sprite.png") no-repeat -239px -203px;
    }
    &-phone {
      color: #fff;
      letter-spacing: .5px;
    }
  }

  .flex {
    align-items: center;
    justify-content: space-between;
  }
  .burger {
    position: relative;
    z-index: 9999;
    padding: 10px;
    cursor: pointer;
    transition-duration: .15s;
    -webkit-transform: matrix(1, 0, 0, 1, 0, 0);
    transform: matrix(1, 0, 0, 1, 0, 0);
  }

  .burger-box {
    width: 30px;
    height: 21px;
    display: -ms-flexbox;
    display: flex;
    position: relative;
    -webkit-transform: matrix(1, 0, 0, 1, 0, 0);
    transform: matrix(1, 0, 0, 1, 0, 0);
  }

  .burger-inner {
    top: 50%;
    margin-top: -1.5px;
    transition-duration: 0.44s;
    transition-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
  }

  .burger-inner:before,
  .burger-inner:after {
    content: '';
    display: block;
  }

  .burger-inner:before {
    top: -9px;
    transition: top 0.2s 0.5s ease-in, opacity 0.2s ease-in;
  }

  .burger-inner:after {
    bottom: -9px;
    transition: bottom 0.2s 0.5s ease-in, transform 0.44s cubic-bezier(0.55, 0.055, 0.675, 0.19);
  }
  .burger-inner,
  .burger-inner:before,
  .burger-inner:after {
    width: 100%;
    height: 3px;
    background: #fff;
    border-radius: 0;
    position: absolute;
  }

  .is-active .burger-inner {
    transform: rotate(225deg);
    transition-delay: .24s;
    transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }

  .is-active .burger-inner:before {
    top: 0;
    opacity: 0;
    transition: top 0.2s ease-out, opacity 0.2s 0.24s ease-out;
  }

  .is-active .burger-inner:after {
    bottom: 0;
    transform: rotate(-90deg);
    transition: bottom 0.2s ease-out, transform 0.44s 0.24s cubic-bezier(0.215, 0.61, 0.355, 1);
  }
  .nav {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    width: calc(100% - 70px);
    z-index: 999;
    display: block;
    &-menu {
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
      padding: 50px 20px 0;
      background: #393939;
      box-shadow: 2px 3px 7px rgba(0,0,0,.2);
      &-item {
        margin-bottom: 10px;
      }
      &-link {
        font-family: 'Roboto', sans-serif;
        font-weight: 100;
        position: relative;
        padding: 10px 0;
        color: #fff;
        font-size: 30px;
        &:after {
          content: '';
          position: absolute;
          bottom: 0;
          display: block;
          width: 0;
          height: 2px;
          background: rgba(#fff,.8);
          transition: .3s .15s ease;
        }
        &:hover:after {
          width: 100%;
        }
      }
    }
  }
  .view-enter-active {
    transition: opacity .5s ease-in-out ,transform .5s ease;
  }
  .view-leave-active {
    transition: opacity 1s ease-in-out, transform 1s ease;
  }
  .view-enter, .view-leave-to {
    opacity: .5;
    transform: translateX(-100%);
  }
  .view-leave, .view-enter-to {
    opacity: 1;
    transform: translateX(0%);
  }

  ////////////////////////////
  @media only screen and (min-width: 560px) and (max-width:1200px) {
    .nav {
      width: 100%;
      top: 51px;
      &-menu {
        flex-direction: row;
        justify-content: space-evenly;
        align-content: flex-start;
        padding-bottom: 40px;
        &-item {
          width: auto;
          padding: 10px;
          flex-shrink: 0;
        }
        &-link {
        }
      }
    }
  }
  @media only screen and (min-width: 1200px) {
    .container {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    .burger {
      display: none;
    }
    .header {
      &-nav {
        background: rgba(#393939,.5);
        padding: 0;
        margin-bottom: 38px;
      }
      &-logo {
        margin-right: 18px;
      }
    }
    .nav {
      width: auto;
      display: block !important;
      position: relative;
      &-menu {
        height: 100%;
        display: flex;
        flex-direction: row;
        padding: 0;
        background: none;
        box-shadow: none;
        &-item {
          margin-bottom: 0;
        }
        &-link {
          display: block;
          padding: 15px 28px;
          color: #fff;
          font-size: 15px;
          font-weight: 500;
          &:after {
            content: none;
          }
          &:hover {
            background: #000;
          }
        }
      }
    }
  }
</style>



