<template>
    <section>
      <p class="section-caption">У вас уже есть свой сайт. Замечательно,
        половина пути пройдена! Но как сделать так,
        чтобы ваша целевая аудитория нашла<br>
        его и узнала о ваших товарах и услугах?
        Всё просто – заказать SEO-продвижение в компании WEB ALTERNATIVE.</p>
        <ul class="articles">
          <li class="articles-item" v-for="article in articles" :key="article.id" :style="article.bg">
            <a class="articles-item-link" :href="article.url">{{article.topic}}</a>
          </li>
        </ul>
      <Advantages/>
      <section-footer></section-footer>
    </section>
</template>
<script>
  import {mapState} from 'Vuex'
  import Advantages from './Advantages'
  import SectionFooter from './SectionFooter'
  export default {
    components: {
      Advantages,
      SectionFooter
    },
    computed: {
      ...mapState(['articles'])
    }
  }
</script>
<style lang="scss" scoped>
  section {
    padding-top: 40px;
  }
  .section-caption {
    padding: 0 20px;
    text-align: justify;
    font-weight: 500;
    font-size: 20px;
    margin-bottom: 40px;
  }
  .articles {
    display: flex;
    flex-wrap: wrap;
    padding: 10px;
    margin-bottom: 46px;
    background: url('/static/img/bg_section1.jpg');
    &-item {
      width: 100%;
      height: 248px;
      padding: 13px;
      background-size: cover !important;
      &:not(:last-child) {
        margin-bottom: 70px;
      }
      &-link {
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        border: 1px solid rgba(#fff,.5);
        color: #fff;
        height: 100%;
        font-size: 20px;
      }
    }
  }
  @media only screen  and (min-width: 640px) and (max-width: 1024px) {
    .articles {
      justify-content: space-between;
      padding-bottom: 0;
      &-item {
        &:not(:last-child) {
          margin-bottom: 10px;
        }
        width: 49%;
      }
    }
  }
  @media only screen  and (min-width: 1024px) {
    .articles {
      flex-wrap: nowrap;
      background: none;
      &-item:not(:last-child) {
        margin-bottom: 0;
      }
    }
  }
  @media only screen and (min-width: 1200px) {
    .container {
      max-width: 1180px;
    }
    section {
      padding-top: 99px;
    }
    .section-caption {
      max-width: 1140px;
      font-size: 18px;
      margin: 0 auto 57px;
      text-align: center;
    }
    .articles {
      flex-wrap: nowrap;
      background: none;
      padding: 0;
      &-item {
        margin-bottom: 0;
        &-link {
          display: block;
          padding: 124px 40px 20px;
        }
      }
    }
  }
</style>



