<template>
  <div class="carousel">
    <div class="carousel-track">
      <div v-for="slide of slides" :key="slide.id" :style="slide.bg">
        <a :href="slide.url"></a>
      </div>
    </div>
    <div class="carousel-control">
      <div class="prev"></div>
      <div class="next"></div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
      }
    },
    computed: {
      slides() {
        let slide = [];
        for (let i = 0; i <= 11; i++) {
           slide.push({id: i, bg: 'background: url(/static/img/icons_sprite.png)', url: '#!'})
        }
        return slide
      }
    }
  }
</script>
<style lang="scss" scoped>
  .carousel {
    padding: 20px;
    position: relative;
    background: rgba(#fff,.6);
    border-radius: 4px;
    &-track {
      display: flex;
      width: 130px;
      overflow: hidden;
      margin: 0 auto;
      div {
        position: relative;
        z-index: 999;
        flex: 1 0 130px;
        height: 65px;
        background-position: 0 -60px !important;
        &:not(:last-child) {
          margin-right: 15px;
        }
        &:hover {
          background-position: -138px -60px !important;
        }
        a {
          display: block;
          position: absolute;
          width: 100%;
          height: 100%;
          top: 0;
          left: 0;
        }
      }
    }
    &-control {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: space-between;
      .next,.prev {
        position: absolute;
        top: 30%;
        width: 50px;
        height: 50px;
        cursor: pointer;
        background: url("/static/img/icons_sprite.png") no-repeat -288px 0px;
        &:hover {
          background-position: -330px 0;
        }
      }
      .prev {
        left: -4%;
        transform: rotate(-180deg);
        &:hover {
          transform: rotate(0deg)
        }
      }
      .next {
        right: -4%;
        &:hover {
          transform: rotate(-180deg)
        }
      }
    }
  }
  @media only screen and (min-width: 480px) {
    .carousel-track {
      width: 100%;
    }
  }
  @media only screen and (min-width: 640px) {
    .carousel-control {
      .next {
        right: -3%;
      }
      .prev {
        left: -3%;
      }
    }
  }
  @media only screen and (min-width: 1200px) {
    .carousel {
      padding: 0;
      &-track {
        justify-content: space-between;
        max-width: calc(125px * 8);
      }
      &-control {
        .next,.prev {
          top: 8px;
        }
        .prev {
          left: -14px;
        }
        .next {
          right: -13px;
        }
      }
    }
  }
</style>



