<template>
  <ul class="bread-crumbs flex">
    <li class="bread-crumbs-link" v-for="link of links" @click="">{{link}}</li>
  </ul>
</template>
<script>
  export default {
    data() {
      return {
      }
    },
    computed: {
      links() {
        return ['Главная', 'Услуги', 'SEO(продвижение в поисковых системах'];
      }
    }
  }
</script>
<style lang="scss" scoped>
  .bread-crumbs {
    display: none;
  }
  @media only screen and (min-width: 1200px) {
    .bread-crumbs {
      display: flex;
      margin-left: 289px;
      &-link {
        position: relative;
        font-size: 15px;
        color: #7e7e7e;
        margin-right: 10px;
        &:not(:last-child):after {
          content: '|';
          position: absolute;
          right: -8px;
          top: 0;
        }
        &.active {
          color: #fff;
        }
      }
    }
  }
</style>



